import { useState } from "react";
import useProducts from "../../../app/hooks/useProducts";
import { useAppDispatch } from "../../../app/store/configStore";
import { Product } from "../../../app/models/product";
import agent from "../../../app/api/agent";
import ProductForm from "./ProductForm";
import { removeProduct, setPageNumber } from "./productSlice";
import { toast } from "react-toastify";
import { Box, Button, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import { LoadingButton } from "@mui/lab";
import AppPagination from "../../../app/components/AppPagination";
import { currencyFormat } from "../../../app/utility/utils";
import ProductSearchInput from "./ProductSearchInput";

export default function ProductInventory() {
    const {products, metaData} = useProducts();
    const dispatch = useAppDispatch();
    const [editMode, setEditMode] = useState(false);
    const [selectedProduct, setSelectedProduct] = useState<Product | undefined>(undefined);
    const [loading, setLoading] = useState(false);
    const [target, setTarget] = useState(0);

    function handleSelectProduct(product: Product) {
        setSelectedProduct(product);
        setEditMode(true);
    }

    function handleDeleteProduct(id: number) {
        setLoading(true);
        setTarget(id)
        agent.Products.removeProduct(id)
            .then(() => dispatch(removeProduct(id)))
            .catch(error => toast.error(error))
            .finally(() => setLoading(false))
    }

    function cancelEdit() {
        if (selectedProduct) setSelectedProduct(undefined);
        setEditMode(false);
    }

    if (editMode) return <ProductForm product={selectedProduct} cancelEdit={cancelEdit} />
    
    return (
        <>
        <Box display='flex' justifyContent='space-between'>
           <Typography sx={{ p: 2 }} variant='h4'>Product Inventory</Typography>
           <Button onClick={() => setEditMode(true)} sx={{ m: 2 }} size='large' variant='contained'>Create</Button>
       </Box>

        <Box sx={{marginBottom: 1}}>
          <Paper sx={{width: '600px'}}>
            <ProductSearchInput />
          </Paper>
        </Box>

       <TableContainer component={Paper}>
           <Table sx={{ minWidth: 650 }} aria-label="simple table">
               <TableHead>
                   <TableRow>
                       <TableCell align="center">Product</TableCell>
                       <TableCell align="left">Price</TableCell>
                       <TableCell align="left">Type</TableCell>
                       <TableCell align="left">Quantity</TableCell>
                       <TableCell align="right"></TableCell>
                   </TableRow>
               </TableHead>
               <TableBody>
                   {products.map((product) => (
                       <TableRow
                           key={product.id}
                           sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                       >
                           <TableCell align="left">
                               <Box display='flex' alignItems='center'>
                                   <img src={product.pictureUrl} alt={product.name} style={{ height: 50, marginRight: 20 }} />
                                   <span>{product.name}</span>
                               </Box>
                           </TableCell>
                           <TableCell align="left">{currencyFormat(product.price)}</TableCell>
                           <TableCell align="left">{product.agriType}</TableCell>
                           <TableCell align="left">{product.quantity}</TableCell>
                           <TableCell align="right">
                               <Button onClick={() => handleSelectProduct(product)} startIcon={<Edit />} />
                               <LoadingButton 
                                   loading={loading && target === product.id} 
                                   onClick={() => handleDeleteProduct(product.id)} 
                                   startIcon={<Delete />} color='error' />
                           </TableCell>
                       </TableRow>
                   ))}
               </TableBody>
           </Table>
       </TableContainer>
       {metaData && 
           <Box sx={{pt: 2}}>
               <AppPagination 
                   metaData={metaData} 
                   onPageChange={(page: number) => dispatch(setPageNumber({pageNumber: page}))} />
           </Box>
       }        
   </>
    )
}